import React from 'react'

function Clock(props) {

return (
    <div id="whole-app">

        <header id="Header">
            <img src={props.logo} className="App-logo" alt="logo" />
            <h1>Steven's Pomodoro Clock</h1>
        </header>

        <main id="Main">

                <div className="flex-third">

                    <h3 id="break-label">Break Length</h3>
                    <p class="incDec" id="break-increment" onClick={props.breakIncrement}>+</p>
                    <p id="break-length">{props.breakTime}</p>
                    <p class="incDec" id="break-decrement" onClick={props.breakDecrement}>-</p>
                    
                </div>

                <div className="flex-third" id="timer">

                    <h3 id="timer-label">Session</h3>

                    <div id="time-left">
                    25:00
                    </div>

                    <div className="timer-buttons">
                    <p id="start_stop">Start/Stop</p>
                    <p id="reset">Reset</p>
                    </div>

                </div>

                <div className="flex-third">

                    <h3 id="session-label">Session Length</h3>
                    <p class="incDec" id="session-increment" onClick={props.seshIncrement}>+</p>
                    <p id="session-length">{props.seshTime}</p>
                    <p class="incDec" id="session-decrement" onClick={props.seshDecrement}>-</p>

                </div>

        </main>
    </div>
);
}

export default Clock