import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Clock from './Clock'

class App extends Component {
  constructor(){
    super()
    this.state = {
      "breakTime": 5,
      "seshTime": 25
    }
    this.handleBreakIncrement = this.handleBreakIncrement.bind(this)
    this.handleBreakDecrement = this.handleBreakDecrement.bind(this)
    this.handleSeshIncrement = this.handleSeshIncrement.bind(this)
    this.handleSeshDecrement = this.handleSeshDecrement.bind(this)
  }
  handleBreakIncrement() {
    this.setState(prevState => {
      return {breakTime: prevState.breakTime + 1}
    })
  }
  handleBreakDecrement() {
    if (this.state.breakTime === 0) {
      this.setState({breakTime: 0})
    }
    else {
      this.setState(prevState => {
        return {breakTime: prevState.breakTime - 1}
      })
    }
  }
  handleSeshIncrement() {
    this.setState(prevState => {
      return {seshTime: prevState.seshTime + 1}
    })
  }
  handleSeshDecrement() {
    if (this.state.seshTime === 0){
      this.setState({seshTime: 0})
    }
    else {
      this.setState(prevState => {
        return {seshTime: prevState.seshTime - 1}
      })
    }
  }
  render() {
    return (
      <Clock
        logo = {logo}
        breakIncrement = {this.handleBreakIncrement}
        breakTime = {this.state.breakTime}
        breakDecrement = {this.handleBreakDecrement}
        seshIncrement = {this.handleSeshIncrement}
        seshTime = {this.state.seshTime}
        seshDecrement = {this.handleSeshDecrement}
      />
    );
  }
}

export default App;
